import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import styled from 'styled-components';
import hrImg from '../assets/hr-12.png';
import buildingImg from '../assets/b3.png';
import Image from 'react-bootstrap/Image';
import {Element} from 'react-scroll';

const Styles = styled.div`
    .container {
        margin-top: 80px;
        margin-bottom: 80px;
    }
    .container h2 {
        text-align: center;
    }
    .styledHr {
        height: 6px;
        background: url(${hrImg}) repeat-x 0 0;
        border: 0;
        max-width: 10rem;
    }
    .wrappingContent {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-flow: column;
    }
    .description {
        box-sizing: border-box;
        width: 100%;
        margin-top: 0.5rem;
        background-color: #dedede;
        box-shadow: 2px 1px 2px rgba(0,0,0,0.5);
        border-radius: 5px;
    }
    .description p {
        text-align: center;
        margin-top: 5px;
    }
`;

const about = () => {
    return <Element name="about" id="aboutId">
        <Styles>
            <Container>
                <h2>Descrio do projeto</h2>
                <hr className="styledHr"></hr>
                <Row>
                    <Col lg={{ span: 6, offset: 2 }} md={8}>
                        <p>
                        Considere um grande e movimentado complexo de edifcios comerciais de escritrios. Por
						motivos de segurana,  necessrio realizar o controle de acesso de todas as pessoas que
						frequentam o complexo habitual (funcionrios) ou esporadicamente (visitantes, clientes etc.).
						Polticas de acesso diferentes so aplicveis a funcionrios da administrao do condomnio,
						funcionrios das empresas que possuem escritrios no local e visitantes em geral. Na entrada do
						complexo, h um conjunto de pontos de acesso (como catracas eletrnicas), por meio dos quais
						as pessoas se identificam (usando biometria, por exemplo) antes de serem autorizadas a entrar
						nas instalaes. Pontos de acesso tambm so instalados na entrada de cada prdio e na
						entrada de cada andar de um prdio, uma vez que funcionrios das empresas condminas,
						assim como os visitantes, s devem ter acesso a certas partes do complexo. Finalmente,
						tambm por motivos de segurana em caso de emergncias, o complexo possui uma
						capacidade de lotao mxima total, por prdio e por andar. Desta forma, um visitante, mesmo
						que possua as credenciais necessrias, s pode ser admitido no complexo, prdio ou andar se
						as respectivas capacidades mximas no tiverem sido excedidas. Note que funcionrios no
						esto sujeitos a este controle de lotao, embora devam ser contados para fins de clculo da
						capacidade de ocupao disponvel. Os parmetros do sistema, em particular as polticas de
						acesso aplicveis aos trs tipos de ocupantes do edifcio e as capacidades mximas (dos
						complexo, dos edifcios e de cada andar), devem ser definidos no incio da operao do sistema
						pelo(a) administrador(a) do condomnio, podendo ser reajustados por ele(a) a qualquer momento.
						</p>
                        <p><b>Links Para o repositrio da aplicao</b></p>
                        <div style={{
                            display: "flex",
                            flexWrap: "wrap",
                            flexFlow: "row",
                            justifyContent: "flex-start",
                            alignItems: "flex-start"
                        }}>
                            <a className="btn btn-primary" style={{marginRight: '20px'}} href="https://github.com/gabrielsxp/project-building-api" target="_blank" rel="noopener noreferrer">Servidor</a>
                            <br />
                            <a className="btn btn-primary" href="https://github.com/gabrielsxp/building-complex-fe" target="_blank" rel="noopener noreferrer">Cliente</a>
                        </div>
                    </Col>
                    <Col lg={4} md={4}>
                        <div className="wrappingContent">
                            <Image src={buildingImg} thumbnail />
                            <div className="description">
                                <p>Exemplo de um prdio na aplicao</p>
                            </div>
                        </div>
                    </Col>
                </Row>
            </Container>
        </Styles>
    </Element>
}

export default about;