import React from 'react';
import Button from 'react-bootstrap/Button';
import {Link} from 'react-router-dom';

const notFound = () => {
    return <div style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100vh',
        padding: '20px'
    }}>
        <div style={{
            display: 'flex',
            flexWrap: 'wrap',
            flexFlow: 'column'
        }}>
            <h2>Pgina No encontrada !</h2>
            <Link to="/app"><Button variant="primary">Voltar ao incio</Button></Link>
        </div>
    </div>
}

export default notFound;